import Vue from 'vue'
import Vuex from 'vuex'
import { db } from '@/firebase/firebase.js'
import { collection, getDocs } from 'firebase/firestore'


Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    courses: [],
    user:''
  },
  getters: {
    totalCourses: state=>{
      return state.courses.reduce((total,course)=>{
        return total + parseInt(course.cupos)
      },0)
    },
    totalIns: state=>{
      return state.courses.reduce((total,course)=>{
        return total + parseInt(course.inscritos)
      },0)
    },
    totalTerm: state=>{
        let terminado = state.courses.filter((course)=>{
          return course.completado === true
        })
      return terminado.length
    },
    totalOpen:state=>{
      let abierto = state.courses.filter((course)=>{
         return course.completado === false
      })
      return abierto.length
    }
  },
  mutations: {
    ADD_COURSE(state, course) {
      state.courses.push({
        id: course.id,
        nombre: course.data().nombre,
        url: course.data().url,
        costo: course.data().costo,
        cupos: course.data().cupos,
        duracion: course.data().duracion,
        completado: course.data().completado,
        descripcion: course.data().descripcion,
        inscritos: course.data().inscritos,
        fecha: course.data().fecha,
        codigo: course.data().codigo
      })
    }, RESET_COURSE(state) {
      state.courses = []
    },
    ADD_USER(state,usuario){
      state.user = usuario
    }
  },
  actions: {
    async fetchCourses({ commit }) {
      try {
        let querySnapShot = await getDocs(collection(db, 'cursos'))
        commit('RESET_COURSE')
        querySnapShot.forEach((doc) => {
          commit('ADD_COURSE', doc)

        })
      } catch (error) {
        console.log(error)
      }

    },
    add_user({commit},newuser){
      commit('ADD_USER',newuser)
    }
  },
  modules: {
  }
})
