    <template>

    <v-container>
        <v-row>
            <v-col class="d-flex justify-center">
                <h1>Editando el curso: {{form.nombre}}</h1>
            </v-col>
        </v-row>
        <v-row>
            <v-col md="4" class="mx-auto">
                <v-form ref="form" v-model="valid" lazy-validation>
                    <v-text-field v-model="form.nombre" :rules="nameRules" placeholder="Nombre" :counter="40" required>
                    </v-text-field>
                    <v-text-field v-model="form.url" :rules="urlRules" placeholder="URL de la imagen del curso"
                        required>
                    </v-text-field>
                    <v-text-field v-model="form.cupos" :rules="cuposRules" label="Cupos del curso" type="number"
                        required>
                    </v-text-field>
                    <v-text-field v-model="form.inscritos" :rules="inscRules" label="Inscritos en el curso"
                        type="number" required>
                    </v-text-field>
                    <v-text-field v-model="form.duracion" :rules="durRules" placeholder="Duración del curso" type="text"
                        required>
                    </v-text-field>
                    <v-text-field v-model="form.costo" :rules="costoRules" label="Costo del curso" type="number"
                        required>
                    </v-text-field>
                    <v-text-field v-model="form.codigo" :rules="codRules" placeholder="Código del curso" required>
                    </v-text-field>
                    <v-textarea v-model="form.descripcion" :rules="descRules" placeholder="Descripción del curso"
                        :counter="240" required>
                    </v-textarea>
                    <v-text-field v-model="form.fecha" :rules="fechaRules" label="Fecha de Registro" required>
                    </v-text-field>
                    <v-switch label="Terminado" color="info" v-model="form.completado"></v-switch>
                    <div>
                        <v-btn color="success" class="mr-4 btn-det" @click="editCommand">
                            GUARDAR
                        </v-btn>
                        <v-btn color="error" class="mr-4" to="/admin">
                            CANCELAR
                        </v-btn>
                    </div>
                    <v-dialog v-model="dialogEdit" max-width="500px">
                            <v-card>
                                <v-card-title class="text-h5">¿Estás seguro de editar este registro?</v-card-title>
                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn color="blue darken-1" text @click="dialogEdit = false">Cancelar</v-btn>
                                    <v-btn color="blue darken-1" text @click="editCourse">Registrar</v-btn>
                                    <v-spacer></v-spacer>
                                </v-card-actions>
                            </v-card>
                        </v-dialog>
                </v-form>
            </v-col>
        </v-row>
    </v-container>

</template>

<script>
import { db } from '@/firebase/firebase.js'
import { doc, updateDoc, getDoc } from 'firebase/firestore'

export default {
    name: 'form-course',
    props: ['idCurso'],
    data: function () {
        return {
            valid: true,
            dialogEdit: false,
            form: {
                nombre: '',
                url: '',
                cupos: '',
                inscritos: '',
                duracion: '',
                costo: '',
                codigo: '',
                descripcion: '',
                completado: ''
            },
            nombre: '',
            nameRules: [
                (v) => !!v || 'Nombre es requerido',
                (v) =>
                    (v && v.length <= 40) || 'Contraseña debe tener al menos 40 caracteres',
            ],
            url: '',
            urlRules: [
                (v) => !!v || 'URL es requerido',
                //(v) => /^(http|https):\/\/[w\d]+\.[\w](\/[\w\d]+)?$/.test(v) || 'URL debe ser válido',
            ],
            cupos: '',
            cuposRules: [
                (v) => !!v || 'Cupo es requerido',
                (v) => /[0-9]{1,3}/.test(v) || 'Ingrese número',
            ],
            inscritos: '',
            inscRules: [
                (v) => !!v || 'Inscritos es requerido',
                (v) => /[0-9]{1,3}/.test(v) || 'Ingrese número',
            ],
            duracion: '',
            durRules: [
                (v) => !!v || 'Duración es requerido',
                (v) =>
                    (v && v.length <= 10) || 'Ingresa duración del curso',
            ],
            costo: '',
            costoRules: [
                (v) => !!v || 'Costo es requerido',
                (v) => /[0-9]{1,3}/.test(v) || 'Ingrese valor del costo',
            ],
            codigo: '',
            codRules: [
                (v) => !!v || 'Código es requerido'
            ],
            descripcion: '',
            descRules: [
                (v) => !!v || 'Descripción es requerido',
                (v) =>
                    (v && v.length <= 240) || 'Ingrese descripción del curso, máximo 240 caracteres',
            ],
            fecha: '',
            fechaRules: [
                (v) => !!v || 'Fecha es requerida'
            ],
        }
    },
    // computed: {},
    methods: {
        async fetchCourseById() {
            const docRef = doc(db, 'cursos', this.idCurso)
            let response = await getDoc(docRef)
            this.form = response.data()
            console.log(this.form)
        },

        async editCourse() {
            this.$refs.form.validate()
            if (!this.valid) return
            try {
                let course = this.form
                course.id = this.idCurso
                await updateDoc(doc(db, 'cursos', course.id), course)
                this.$router.push('/admin')
            }
            catch (error) {
                console.log(error)
            }
        },
        idCurs() {
            return this.form.id = this.idCurso
        },
        editCommand() {
            this.dialogEdit = true
        }
    },
    // watch: {},
    // components: {},
    // mixins: [],
    // filters: {},
    // -- Lifecycle Methods
    created() {
        this.fetchCourseById()
    }
    // -- End Lifecycle Methods
}
</script>

<style scoped>
h1{
    font-size: 3.125rem;
    margin: 0.9375rem 0rem;
}
</style>