<template>
    <v-row justify="center">
        <v-dialog :value="dialog" persistent max-width="600px">
            <v-card>
                <v-card-title class="d-flex justify-center">
                    <span class="text-h4">AGREGAR CURSO</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                        <v-row>
                            <v-col>
                                <v-form ref="form" v-model="valid" lazy-validation>
                                    <v-text-field v-model="form.nombre" :rules="nameRules" outlined placeholder="Nombre"
                                        :counter="40" required>
                                    </v-text-field>
                                    <v-text-field v-model="form.url" :rules="urlRules" outlined
                                        placeholder="URL de la imagen del curso" required>
                                    </v-text-field>
                                    <v-text-field v-model="form.cupos" :rules="cuposRules" outlined
                                        label="Cupos del curso" type="number" required>
                                    </v-text-field>
                                    <v-text-field v-model="form.inscritos" :rules="inscRules" outlined
                                        label="Inscritos en el curso" type="number" required>
                                    </v-text-field>
                                    <v-text-field v-model="form.duracion" :rules="durRules" outlined
                                        placeholder="Duración del curso" type="text" required>
                                    </v-text-field>
                                    <v-text-field v-model="form.costo" :rules="costoRules" outlined
                                        label="Costo del curso" required>
                                    </v-text-field>
                                    <v-text-field v-model="form.codigo" :rules="codRules" outlined
                                        placeholder="Código del curso" required>
                                    </v-text-field>
                                    <v-textarea outlined v-model="form.descripcion" :rules="descRules"
                                        placeholder="Descripción del curso" :counter="240" required>
                                    </v-textarea>
                                    <v-switch :label="`Terminado: ${form.completado ? 'Si' : 'No'}`" color="info"
                                        v-model="form.completado"></v-switch>
                                </v-form>
                            </v-col>
                        </v-row>
                    </v-container>

                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <div class="d-flex justify-center">
                        <v-btn color="success" class="mr-4 btn-det" @click="createCourse">
                            AGREGAR
                        </v-btn>
                        <v-btn color="error" class="mr-4" @click="closeDialog">
                            CANCELAR
                        </v-btn>
                    </div>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </v-row>
</template>

<script>
import { db } from '@/firebase/firebase.js'
import { collection, addDoc } from 'firebase/firestore'

export default {
    name: 'form-course',
    props: {
        dialog: {
            type: Boolean,
            require: true
        }
    },
    data: function () {
        return {
            valid: true,
            form: {
                nombre: '',
                url: '',
                cupos: '',
                inscritos: '',
                duracion: '',
                costo: '',
                codigo: '',
                descripcion: '',
                completado: ''
            },
            nombre: '',
            nameRules: [
                (v) => !!v || 'Nombre es requerido',
                (v) =>
                    (v && v.length <= 40) || 'Contraseña debe tener al menos 40 caracteres',
            ],
            url: '',
            urlRules: [
                (v) => !!v || 'URL es requerido',
                //(v) => /^(http|https):\/\/[w\d]+\.[\w](\/[\w\d]+)?$/.test(v) || 'URL debe ser válido',
            ],
            cupos: '',
            cuposRules: [
                (v) => !!v || 'Cupo es requerido',
                (v) => /[0-9]{1,3}/.test(v) || 'Ingrese número',
            ],
            inscritos: '',
            inscRules: [
                (v) => !!v || 'Inscritos es requerido',
                (v) => /[0-9]{1,3}/.test(v) || 'Ingrese número',
                (v) => Number(v) <= this.form.cupos || 'El número de inscrito debe ser menor o igual al número de cupos'
            ],
            duracion: '',
            durRules: [
                (v) => !!v || 'Duración es requerido',
                (v) =>
                    (v && v.length <= 10) || 'Ingresa duración del curso',
            ],
            costo: '',
            costoRules: [
                (v) => !!v || 'Costo es requerido',
                (v) => /[0-9]{1,3}/.test(v) || 'Ingrese valor del costo',
            ],
            codigo: '',
            codRules: [
                (v) => !!v || 'Código es requerido',
                (v) => /[0-9]{1,10}/.test(v) || 'Ingrese número correspondiente al código',
            ],
            descripcion: '',
            descRules: [
                (v) => !!v || 'Descripción es requerido',
                (v) =>
                    (v && v.length <= 240) || 'Ingrese descripción del curso, máximo 240 caracteres',
            ]
        }
    },
    // computed: {},
    methods: {
        closeDialog() {
            this.$emit('close')
        },
        async createCourse() {
            this.$refs.form.validate()
            if (!this.valid) return

            try {
                let response = await addDoc(collection(db, 'cursos'), this.form)
                console.log(response)
                alert('El curso ha sido agregado')
                this.$router.go()
            }
            catch (error) {
                console.log(error)
            }
        },
        setDate() {
            const d = new Date();
            let text = d.toLocaleDateString('es-CL');
            this.form.fecha = text
        },
        changeSwitch() {
            if (this.form.completado == true) {
                return this.form.completado = 'Si'
            } else {
                return this.form.completado = 'No'
            }
        }
    },
    // watch: {},
    // components: {},
    // mixins: [],
    // filters: {},
    // -- Lifecycle Methods
    created() {
        this.setDate()
        this.changeSwitch()
    }
    // -- End Lifecycle Methods
}
</script>

<style scoped>
</style>