<template>
    <div>
        <div class="big-box-1">
            <v-data-table :headers="headers" :items="courses" class="elevation-1">
                <template v-slot:top>
                    <v-toolbar flat>
                        <v-toolbar-title>Mis Usuarios Registrados</v-toolbar-title>
                        <v-btn class="btn-add-det" dark color="teal" @click="dialog = true">
                            <v-icon left>
                                mdi-plus
                            </v-icon>Nuevo curso
                        </v-btn>
                        <v-divider class="mx-4" inset vertical></v-divider>
                        <v-spacer></v-spacer>

                        <v-dialog v-model="dialogDelete" max-width="500px">
                            <v-card>
                                <v-card-title class="text-h5">¿Estás seguro de borrar este registro?</v-card-title>
                                <v-card-actions>
                                    <v-spacer></v-spacer>
                                    <v-btn color="blue darken-1" text @click="dialogDelete = false">Cancelar</v-btn>
                                    <v-btn color="blue darken-1" text @click="deleteCourse">Borrar</v-btn>
                                    <v-spacer></v-spacer>
                                </v-card-actions>
                            </v-card>
                        </v-dialog>
                    </v-toolbar>
                </template>
                <template v-slot:[`item.completado`]="{ item }">
                    <v-chip dark :color="item.completado ? 'primary' : 'gray'">{{ item.completado ? 'Si' : 'No' }}
                    </v-chip>
                </template>
                <template v-slot:[`item.costo`]="{ item }">
                    <v-chip dark color="green">${{ Number(item.costo).toLocaleString('es-CL') }}</v-chip>
                </template>
                <template v-slot:[`item.fecha`]="{ item }">
                    <v-chip dark color="green">{{ item.fecha }}</v-chip>
                </template>
                <template v-slot:[`item.actions`]="{ item }">
                    <v-icon small class="mr-2" @click="$router.push(`/curso/${item.id}`)">
                        mdi-pencil
                    </v-icon>
                    <v-icon small @click="deleteCommand(item.id)">
                        mdi-delete
                    </v-icon>
                </template>
            </v-data-table>
            <form-course :dialog="dialog" @close="dialog = false"></form-course>
        </div>
        <div>
            <v-container>
                <v-row class="d-flex justify-center">
                    <v-col md="4">
                        <v-alert shaped outlined color="teal darken-3" icon="mdi-account-group">
                            Cantidad total de alumnos permitidos: <span class="font-weight-bold">{{ totalCourses
                            }}</span>
                            alumnos
                        </v-alert>
                        <v-alert shaped outlined color="orange darken-4" icon="mdi-account-multiple-check">
                            Cantidad total de alumnos inscritos: <span class="font-weight-bold">{{ totalIns }}</span>
                            alumnos
                        </v-alert>
                        <v-alert shaped outlined color="light-blue darken-3" icon="mdi-account-clock">
                            Cantidad total de cupos restantes: <span class="font-weight-bold">{{ cupRest }}</span>
                            alumnos
                        </v-alert>
                        <v-alert shaped outlined color="red darken-3" icon="mdi-account-cancel">
                            Cantidad total de cursos terminados: <span class="font-weight-bold">{{totalTerm}}</span> cursos
                        </v-alert>
                        <v-alert shaped outlined color="green darken-3" icon="mdi-bell-alert">
                            Cantidad total de cursos activos: <span class="font-weight-bold">{{totalOpen}}</span> cursos
                        </v-alert>
                        <v-alert shaped outlined color="deep-purple darken-3" icon="mdi-bell">
                            Cantidad total de cursos: <span class="font-weight-bold">{{ totalCurso }}</span> cursos
                        </v-alert>
                    </v-col>
                </v-row>
            </v-container>
        </div>
    </div>
</template>

<script>
import { db } from '@/firebase/firebase.js'
import { doc, deleteDoc } from 'firebase/firestore'
import { mapActions, mapState, mapGetters } from 'vuex'
import FormCourse from '@/components/FormCourse.vue'


export default {
    name: 'admin-view',
    // props: {},
    data: function () {
        return {
            idDelete: '',
            dialogDelete: false,
            headers: [
                {
                    text: 'CURSO',
                    align: 'start',
                    sortable: false,
                    value: 'nombre',
                },
                { text: 'CUPOS', value: 'cupos' },
                { text: 'INSCRITOS', value: 'inscritos' },
                { text: 'DURACIÓN', value: 'duracion' },
                { text: 'COSTO', value: 'costo' },
                { text: 'TERMINADO', value: 'completado' },
                { text: 'FECHA', value: 'fecha' },
                { text: 'ACCIONES', value: 'actions', sortable: false },
            ],
            dialog: false
        }
    },
    computed: {
        ...mapState(['courses']),
        ...mapGetters(['totalCourses', 'totalIns', 'totalTerm', 'totalOpen']),
        cupRest() {
            return this.totalCourses - this.totalIns
        },
        totalCurso() {
            return this.courses.length
        }
    },
    methods: {
        ...mapActions(['fetchCourses']),
        async deleteCourse() {
            try {
                await deleteDoc(doc(db, 'cursos', this.idDelete))
                this.dialogDelete = false
                //this.$router.go()
                this.fetchCourses()
            }
            catch (error) {
                console.log(error)
            }
        },
        deleteCommand(id) {
            this.dialogDelete = true
            this.idDelete = id
        }
    },
    // watch: {},
    components: {
        'form-course': FormCourse
    },
    // mixins: [],
    // filters: {},
    // -- Lifecycle Methods
    created() {
        this.fetchCourses()
    }
    // -- End Lifecycle Methods
}
</script>

<style scoped>
.big-box-1 {
    margin: 3.125rem 1.5625rem 5rem 1.5625rem;
}
.btn-add-det{
    margin-left: 3.125rem;
}
</style>