<template>
    <v-container class="big-box">
        <v-row>
            <v-col class="d-flex justify-center">
                <h1>LOGIN</h1>
            </v-col>
        </v-row>
        <v-row class="d-flex justify-center">
            <v-col md="3">
                <v-form ref="form" v-model="valid" lazy-validation>
                    <v-text-field v-model="email" :rules="emailRules" label="Correo" required></v-text-field>

                    <v-text-field v-model="password" :rules="passRules" label="Contraseña" required type="password">
                    </v-text-field>
                    <div class="d-flex justify-center">
                        <v-btn :disabled="!valid" color="success" class="mr-4 btn-det" @click="login">
                            Ingresar
                        </v-btn>
                        <v-btn color="error" class="mr-4" @click="reset">
                            Limpiar
                        </v-btn>
                    </div>
                </v-form>
            </v-col>
        </v-row>
    </v-container>

</template>

<script>
import { getAuth, signInWithEmailAndPassword } from "firebase/auth";
import { mapActions } from "vuex";
export default {
    name: 'login-view',
    // props: {},
    data: function () {
        return {
            valid: true,
            password: '',
            passRules: [
                (v) => !!v || 'Contraseña es requerida',
                (v) =>
                    (v && v.length <= 6) || 'Contraseña debe tener al menos 6 caracteres',
            ],
            email: '',
            emailRules: [
                (v) => !!v || 'Email es requerido',
                (v) => /.+@.+\..+/.test(v) || 'Email debe ser válido',
            ]
        }
    },
    // computed: {},
    methods: {
        validate() {
            this.$refs.form.validate()
        },
        login() {
            this.validate()
            if (this.valid == false) return

            const auth = getAuth()
            signInWithEmailAndPassword(auth, this.email, this.password)
                .then((userCredential) => {
                    // Signed in
                    const user = userCredential.user
                    console.log(user)
                    this.add_user(this.email)
                    this.$router.push('/')
                    // ...
                })
                .catch((error) => {
                    //const errorCode = error.code
                    const errorMessage = error.message
                    alert(errorMessage)

                })
        },
        reset(){
            this.email = "",
            this.password= ""
        },
        ...mapActions(['add_user'])
    }
    // watch: {},
    // components: {},
    // mixins: [],
    // filters: {},
    // -- Lifecycle Methods
    // -- End Lifecycle Methods
}
</script>

<style scoped>

.big-box{
    margin-top: 12.5rem;
}
</style>